import * as tf_tsne from 'tfjs-tsne';
import {tfc as tfc} from 'tfjs-tsne';

console.log('Texture tSNE with TensorFlow.js');

// Gradient Descent parameters
let iterations = 0;
let alphaViz = 0.25;
const maxExaggeration = 4;
let exaggerationMult = 1;

let init = false;

// UI elements
const iterateChkBx = document.getElementById('iterateChkBx');
const textureChkBx = document.getElementById('textureChkBx');
const alphaSlider = document.getElementById('alphaSlider');

// Infomation
const numPoints = 60000;

// initializing the tSNE class and the canvas drawer
const tsne = new tf_tsne.TSNEOptimizer(numPoints, false);
const drawer = new tf_tsne.EmbeddingDrawer(numPoints);

// Precomputed Probabilites
// can be loaded from the server
const pntsPerRow = tsne.pointsPerRow;
const numRows = tsne.numRows;
const numNeighPerRow = 2794;

// The canvas used by TensorFlow.js is added to the page to enable the use of
// custom drawers.
const backend = tfc.ENV.findBackend('webgl');
const canvas = backend.getCanvas();
document.getElementById('canvas-div').appendChild(canvas);
canvas.height = 800;
canvas.width = 800;

const gpgpu = backend.getGPGPUContext();
const webglContext = backend.getGPGPUContext().gl;
tfc.webgl.webgl_util.bindCanvasToFramebuffer(webglContext);
webglContext.clearColor(1, 1, 1, 1);
webglContext.clear(webglContext.COLOR_BUFFER_BIT);

// Joint probability definition
let offsets;
let probabilities;
let neighIds;
let colors;
let mnistTexture;

// Off screen render target
const tempTexture = tf_tsne.gl_util.createAndConfigureInterpolatedTexture(
    gpgpu.gl, 800, 800, 4);

/**
 * Draw the embedding on canvas
 */
function draw() {
  const embedding = {
    minX : tsne.minX,
    maxX : tsne.maxX,
    minY : tsne.minY,
    maxY : tsne.maxY,
    embeddingTexture : tsne.embeddingTexture,
    pntsPerRow : tsne.numberOfPointsPerRow,
    numRows : tsne.numberOfRows,
    numPoints : tsne.numberOfPoints
  };
  if (textureChkBx.checked === false) {
    drawer.drawTexturedPoints(embedding, alphaViz, mnistTexture, 14, colors);
  } else {
    const textureNormalization = Math.sqrt(tsne.normalizationQ);
    drawer.drawColoredPoints(embedding, alphaViz, colors, tempTexture);
    drawer.drawPointsAndSplatTexture(embedding, tsne.splatTexture, tempTexture,
                                     textureNormalization);
  }
}

/**
 * Perform a gradient descent iteration
 */
function iterate() {
  if (init === false) {
    tsne.initializeNeighbors(numNeighPerRow, offsets, probabilities, neighIds);
    init = true;
  }
  if (iterateChkBx.checked === true) {
    requestAnimationFrame(iterate);
  }

  // Setting parameters the SGD parameters
  // with a linear fall-off of the exaggeration
  if (iterations >= 600) {
    tsne.exaggeration = 1.;
  } else if (iterations >= 200) {
    tsne.exaggeration =
        maxExaggeration - (maxExaggeration - 1) * (iterations - 200.) / 400.;
  } else {
    tsne.exaggeration = maxExaggeration;
  }
  tsne.exaggeration *= exaggerationMult;

  tsne.iterate();
  ++iterations;
  if (iterations % 100 === 0) {
    console.log('Iter #' + iterations);
  }

  if (iterations === 1000) {
    console.log('Switching off automatically after 1000 iterations');
    iterateChkBx.checked = false;
  }

  draw();
}

iterateChkBx.addEventListener('click', () => { iterate(); });

textureChkBx.addEventListener('click', () => { draw(); });

const resetBtn = document.getElementById('resetBtn');
resetBtn.addEventListener('click', () => {
  console.log('Reset!');
  tsne.initializeEmbedding();
  iterations = 0;
  exaggerationMult = 1;
  iterate();
  iterate();
  iterateChkBx.checked = false;
});

alphaSlider.addEventListener('input', () => {
  if (iterateChkBx.checked === false) {
    requestAnimationFrame(draw);
  }
  alphaViz = Number(alphaSlider.value) / Number(alphaSlider.max) / 2.;
});

