// Loading the textures -> switch to promises

/**
 * Load the texture oReqOffsets
 */
function loadOffsets() {
  return new Promise(function(resolve, reject) {
    const oReqOffsets = new XMLHttpRequest();
    oReqOffsets.open('GET', document.getElementById('offsets').href, true);
    oReqOffsets.responseType = 'arraybuffer';
    oReqOffsets.onload = (oEvent) => {
      if (oReqOffsets.status >= 200 && oReqOffsets.status < 300) {
        const arrayBufferOffsets = oReqOffsets.response;
        if (arrayBufferOffsets) {
          const byteArrayOffsets = new Float32Array(arrayBufferOffsets);
          offsets = tf_tsne.gl_util.createAndConfigureTexture(
              gpgpu.gl, pntsPerRow, numRows, 3, // offset 2D + numNeigh
              byteArrayOffsets);
        }
        resolve(oReqOffsets.response);
      } else {
        reject(
            {status : oReqOffsets.status, statusText : oReqOffsets.statusText});
      }
    };
    oReqOffsets.onerror = function() {
      reject(
          {status : oReqOffsets.status, statusText : oReqOffsets.statusText});
    };
    oReqOffsets.send();
  });
}

/**
 * Loads the labels as oReqColors
 */
function loadColors() {
  return new Promise(function(resolve, reject) {
    const oReqColors = new XMLHttpRequest();
    oReqColors.open('GET', document.getElementById('colors').href, true);
    oReqColors.responseType = 'arraybuffer';
    oReqColors.onload = (oEvent) => {
      if (oReqColors.status >= 200 && oReqColors.status < 300) {
        const arrayBufferColors = oReqColors.response;
        if (arrayBufferColors) {
          const byteArrayColors = new Float32Array(arrayBufferColors);
          colors = tf_tsne.gl_util.createAndConfigureTexture(
              gpgpu.gl, pntsPerRow, numRows, 3, byteArrayColors);
        }
        resolve(oReqColors.response);
      } else {
        reject(
            {status : oReqColors.status, statusText : oReqColors.statusText});
      }
    };
    oReqColors.onerror = function() {
      reject({status : oReqColors.status, statusText : oReqColors.statusText});
    };
    oReqColors.send();
  });
}

/**
 * Load the oReqProbabilities
 */
function loadProbabilities() {
  return new Promise(function(resolve, reject) {
    const oReqProbabilities = new XMLHttpRequest();
    oReqProbabilities.open('GET', document.getElementById('probabilities').href,
                           true);
    oReqProbabilities.responseType = 'arraybuffer';
    oReqProbabilities.onload = (oEvent) => {
      if (oReqProbabilities.status >= 200 && oReqProbabilities.status < 300) {
        const arrayBufferProb = oReqProbabilities.response;
        if (arrayBufferProb) {
          const byteArrayProb = new Float32Array(arrayBufferProb);
          probabilities = tf_tsne.gl_util.createAndConfigureTexture(
              gpgpu.gl, numNeighPerRow, numNeighPerRow, 1, byteArrayProb);
        }
        resolve(oReqProbabilities.response);
      } else {
        reject({
          status : oReqProbabilities.status,
          statusText : oReqProbabilities.statusText
        });
      }
    };
    oReqProbabilities.onerror = function() {
      reject({
        status : oReqProbabilities.status,
        statusText : oReqProbabilities.statusText
      });
    };
    oReqProbabilities.send();
  });
}

/**
 * Loads the neighbor neighIds
 */
function loadNeighIds() {
  return new Promise(function(resolve, reject) {
    const oReqNeighIds = new XMLHttpRequest();
    oReqNeighIds.open('GET', document.getElementById('neighIds').href, true);
    oReqNeighIds.responseType = 'arraybuffer';
    oReqNeighIds.onload = (oEvent) => {
      if (oReqNeighIds.status >= 200 && oReqNeighIds.status < 300) {
        const arrayBufferNeighIds = oReqNeighIds.response;
        if (arrayBufferNeighIds) {
          const byteArrayNeighIds = new Float32Array(arrayBufferNeighIds);
          neighIds = tf_tsne.gl_util.createAndConfigureTexture(
              gpgpu.gl, numNeighPerRow, numNeighPerRow, 1, byteArrayNeighIds);
        }
        resolve(oReqNeighIds.response);
      } else {
        reject({
          status : oReqNeighIds.status,
          statusText : oReqNeighIds.statusText
        });
      }
    };
    oReqNeighIds.onerror = function() {
      reject(
          {status : oReqNeighIds.status, statusText : oReqNeighIds.statusText});
    };
    oReqNeighIds.send();
  });
}
loadNeighIds().then(
    () => {loadMNIST(webglContext, document.getElementById('mnistImg').href)
               .then(() => {loadOffsets().then(
                         () => {loadProbabilities().then(
                             () => {loadColors().then(function() {
                               iterate();
                               showControls();
                             })})})})});

/**
 * Load the MNIST dataset
 * @param {WebGlContext} gl current context
 * @param {string} url input image url
 * @returns {WebGLTexture} MNISTA dataset textures
 */
function loadMNIST(gl, url) {
  return new Promise(function(resolve, reject) {
    const texture = gl.createTexture();
    gl.bindTexture(gl.TEXTURE_2D, texture);
    const level = 0;
    // tslint:disable-next-line:no-any
    const internalFormat = gl.RGBA8;
    const border = 0;
    // tslint:disable-next-line:no-any
    const srcFormat = gl.RGBA;
    const srcType = gl.UNSIGNED_BYTE;
    const pixel = new Uint8Array([ 0, 0, 255, 255 ]);
    gl.texImage2D(gl.TEXTURE_2D, level, internalFormat, 1, 1, border, srcFormat,
                  srcType, pixel);

    const image = new Image();
    image.onload = () => {
      gl.bindTexture(gl.TEXTURE_2D, texture);
      gl.texImage2D(gl.TEXTURE_2D, level, internalFormat, srcFormat, srcType,
                    image);
      gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_S, gl.CLAMP_TO_EDGE);
      gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_WRAP_T, gl.CLAMP_TO_EDGE);
      gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MIN_FILTER, gl.NEAREST);
      gl.texParameteri(gl.TEXTURE_2D, gl.TEXTURE_MAG_FILTER, gl.NEAREST);
    };
    image.src = url;
    mnistTexture = texture;
    resolve();
  })
}

