function showControls() {
  const hiddenIterDiv = document.querySelector('#hidden-iter-div');
  hiddenIterDiv.classList.remove('hidden');

  const dataLoadingDiv = document.querySelector('#data-loading-div');
  dataLoadingDiv.remove();
}
